import { ArrowRight } from "../Icons/Icons.jsx";
export default function TaskListElement({
  taskName,
  setIsOpenTask,
  taskId,
  setIsOpenTaskPage,
  setCurrentTask,
}) {
  function clickHandler() {
    setCurrentTask(taskId);
    setIsOpenTask(false);
    setIsOpenTaskPage(true);
  }
  return (
    <button className="task-element" onClick={clickHandler}>
      <span style={{ color: "#0d4cd3" }}>{taskName}</span>
      <span>
        <ArrowRight />
      </span>
    </button>
  );
}
