import { useState } from "react";
import Agree from "../Agree/Agree.jsx";
import TaskList from "../TaskList/TaskList.jsx";
export default function Main({
  background,
  themeIconStatus,
  active,
  setActive,
}) {
  const style = {
    width: "100vw",
    background: background,
    flex: "1 0 100%",
  };
  const agreeContent = {
    title: "Тренажёр по неразрывным пробелам",
    buttonName: "Начать",
  };
  const [isAgree, setIsAgree] = useState(false);
  return (
    <main style={style}>
      {!isAgree ? (
        <Agree
          title={agreeContent.title}
          buttonName={agreeContent.buttonName}
          setIsAgree={setIsAgree}
          active={active}
          setActive={setActive}
          themeIconStatus={themeIconStatus}
        />
      ) : (
        <TaskList
          themeIconStatus={themeIconStatus}
          setIsAgree={setIsAgree}
          active={active}
          setActive={setActive}
        />
      )}
    </main>
  );
}
