import { ArrowLeft, ModalBtnHintIcon } from "../Icons/Icons";
import "./Task.css";
import React, { useEffect, useState } from "react";
import Modal from "../Modal/Modal.jsx";
import { test } from "../../data.js";
import SubRender from "../SubRender/SubRender";
export default function Task({
  setIsOpenTaskPage,
  setCurrentTask,
  currentTask,
  themeIconStatus,
  active,
  setActive,
}) {
  const [isOpenPopUp, setIsOpenPopUp] = useState(null);
  const header = "Расставьте неразрывные пробелы";
  const buttonName = "Проверить";
  function clickHandler() {
    setIsOpenTaskPage((prevValue) => (prevValue = false));
    setCurrentTask((prevValue) => (prevValue = null));
  }
  const [userAnswers, setUserAnswers] = useState(
    test[currentTask].answers.reduce((accumulator, element, index, array) => {
      accumulator[index] = new Array(element.length).fill(null);
      return accumulator;
    }, [])
  );
  const [isSelected, setIsSelected] = useState(
    test[currentTask].answers.reduce((accumulator, element, index, array) => {
      accumulator[index] = new Array(element.length).fill(null);
      return accumulator;
    }, [])
  );
  const [modalType, setModalType] = useState(null);
  const [result, setResult] = useState(
    test[currentTask].answers.reduce((accumulator, element, index, array) => {
      accumulator[index] = new Array(element.length).fill(-1);
      return accumulator;
    }, [])
  );

  function awswerHandler(event) {
    const word = event.target.dataset;
    if (word.id) {
    }
  }
  function AnswerHanlder2(
    id,
    paragraph,
    setIsOpenPopUp,
    userAnswers,
    setUserAnswers,
    isOpenPopUp
  ) {
    if (!isOpenPopUp) setIsOpenPopUp([id, paragraph]);
    if (isOpenPopUp) setIsOpenPopUp(null);
    return;
  }
  const [errors, setErrors] = useState(null);

  useEffect(() => {
    if (errors === 0) {
      setModalType("correct");
    }
    if (errors > 0) {
      setModalType("incorrect");
    }
  }, [errors]);

  function handlerCheckButton() {
    setIsSelected(
      test[currentTask].answers.reduce((accumulator, element, index, array) => {
        accumulator[index] = new Array(element.length).fill(null);
        return accumulator;
      }, [])
    );
    if (userAnswers.flat().every((res) => res === null)) {
      setIsOpenPopUp(null);
      setActive(true);
      setModalType("error");
      return;
    }

    setErrors(null);

    setResult(() => {
      return test[currentTask].answers.reduce((accumulator, element, index) => {
        accumulator.push(
          element.reduce((accumulator2, element, secondIndex) => {
            if (userAnswers[index][secondIndex] === null && element === 0) {
              accumulator2[secondIndex] = true;
              setErrors((prevValue) => {
                return prevValue === null ? 0 : prevValue;
              });
              return accumulator2;
            }
            if (element === userAnswers[index][secondIndex]) {
              accumulator2[secondIndex] = true;
              setErrors((prevValue) => {
                return prevValue === null ? 0 : prevValue;
              });
              return accumulator2;
            }
            setErrors((prevValue) => prevValue + 1);
            accumulator2[secondIndex] = false;
            return accumulator2;
          }, [])
        );
        return accumulator;
      }, []);
    });
    setIsOpenPopUp(null);
    setActive(true);
  }
  function openHint() {
    setModalType("hint");
    setActive("true");
  }

  return (
    <>
      {active ? (
        <Modal
          title={"Предупреждение"}
          active={active}
          setActive={setActive}
          themeIconStatus={themeIconStatus}
          modalType={modalType}
          errors={errors}
          clickHandler={clickHandler}
        />
      ) : null}

      <section
        className="task"
        style={{
          display: "flex",
          flexDirection: "column",
          gap: "24px",
        }}
      >
        <button className="back-button" onClick={clickHandler}>
          <ArrowLeft h={"18px"} />
          <span>Назад</span>
        </button>
        <h2
          style={{
            fontWeight: "700",
            fontSize: "24px",
            fontHeight: "32px",
            borderBottom: "1px solid #99b1e6",
            paddingBottom: "24px",
            marginBottom: "0px",
            position: "relative",
          }}
        >
          {header}
          <span
            style={{
              position: "absolute",
              top: "0",
              right: 0,
              width: "24px",
              height: "24px",
              borderRadius: "9999999px",
              backgroundColor: "#E4ECFD",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              cursor: "pointer",
              zIndex: "1",
            }}
            onClick={() => openHint()}
          >
            <ModalBtnHintIcon w={14} themeIconStatus={themeIconStatus} />
          </span>
        </h2>
        <div onClick={awswerHandler} style={{ lineHeight: "24px" }}>
          {
            <SubRender
              content={test[currentTask].testContent}
              AnswerHanlder2={AnswerHanlder2}
              isOpenPopUp={isOpenPopUp}
              setIsOpenPopUp={setIsOpenPopUp}
              result={result}
              setResult={setResult}
              key={Math.random()}
              userAnswers={userAnswers}
              setUserAnswers={setUserAnswers}
              isSelected={isSelected}
              setIsSelected={setIsSelected}
            />
          }
        </div>
        <button className="task-button" onClick={() => handlerCheckButton()}>
          {buttonName}
        </button>
      </section>
    </>
  );
}
