import React from "react";
import "./modal.css";
import { ModalIcons, XIcon } from "../Icons/Icons";
import { taskHintData, taskDescription } from "../../data.js";
export default function Modal({
  active,
  setActive,
  title,
  themeIconStatus,
  modalType,
  errors,
  clickHandler,
}) {
  return (
    <div
      className={active ? "modal active" : "modal"}
      onClick={() => {
        setActive(false);
        document.body.classList.remove("disable-scroll");
      }}
    >
      <div
        className="modal-content"
        onClick={(event) => event.stopPropagation()}
      >
        <ModalContent
          modalType={modalType}
          themeIconStatus={themeIconStatus}
          setActive={setActive}
          errors={errors}
          clickHandler={clickHandler}
        />
      </div>
    </div>
  );
}

function ModalContent({
  modalType,
  themeIconStatus,
  setActive,
  errors,
  clickHandler,
}) {
  document.body.classList.add("disable-scroll");
  let text2;
  let text;
  if (modalType === "error") {
    text = "Не расставлены пробелы";
    text2 = "Вернитесь к заданию и поставьте неразрывные пробелы";
  }
  if (modalType === "correct") {
    text = "Идеально";
  }
  if (modalType === "incorrect") {
    text = wordFixit(errors);
    text2 = "Ознакомьтесь с ошибками и по желанию пройдите задание ещё раз";
  }
  function wordFixit(errorCount) {
    console.log(errorCount);
    if (errorCount === 1) {
      return "ошибка";
    }
    if (errorCount >= 2 && errorCount <= 4) {
      console.log(errorCount);
      return "ошибки";
    }
    if (errorCount >= 5 && errorCount <= 20) {
      return "ошибок";
    }
    if (errorCount > 20) {
      const toEquel = Number(String(errorCount).split("")[1]);
      if (toEquel >= 2 && toEquel <= 4) {
        return "ошибки";
      }
      if ((toEquel >= 5 && toEquel <= 9) || toEquel === 0) {
        return "ошибок";
      }
      if (toEquel === 1) {
        return "ошибка";
      }
    }
  }
  function renderHintText(content) {
    return content.map((el) => (
      <div>
        {el.title ? (
          <p
            style={{ fontSize: "18px", fontWeight: "700", margin: "0 0 12px" }}
          >
            {el.title}
          </p>
        ) : null}
        <el.tag
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "12px",
            lineHeight: "24px",
          }}
        >
          {el.listText.split("\n").map((paragraph, indexOfParagraph) => (
            <li style={{ position: "relative", paddingLeft: "20px" }}>
              <span
                style={{
                  position: "absolute",
                  top: "0",
                  left: "0",
                  color: "rgb(78, 130, 250)",
                  fontWeight: "700",
                }}
              >
                {el.tag === "ul" ? (
                  <span
                    style={{
                      backgroundColor: "#4D83FA",
                      width: "8px",
                      height: "8px",
                      display: "block",
                      borderRadius: "99999px",
                      marginTop: "8px",
                    }}
                  ></span>
                ) : (
                  `${++indexOfParagraph}.`
                )}
              </span>
              {paragraph}
            </li>
          ))}
        </el.tag>
      </div>
    ));
  }
  return (
    <div
      className="error-modal-container"
      style={{
        display: "flex",
        flexDirection: "column",
        gap: "24px",
        alignItems: "center",
      }}
    >
      {modalType === "error" ||
      modalType === "correct" ||
      modalType === "incorrect" ? (
        <div
          style={{ width: "100%", display: "flex", flexDirection: "column" }}
        >
          <span
            onClick={() => {
              setActive(false);
              document.body.classList.remove("disable-scroll");
            }}
            style={{ alignSelf: "end" }}
          >
            <XIcon w={24} h={24} />
          </span>
        </div>
      ) : null}
      {modalType === "hint" || modalType === "task description" ? (
        <div
          style={{
            width: "100%",
            position: "relative",
            paddingBottom: "24px",
            borderBottom: "1px solid #99B1E6",
          }}
        >
          <h2>
            {modalType === "hint" ? taskHintData.title : taskDescription.title}
          </h2>
          <span
            onClick={() => {
              setActive(false);
              document.body.classList.remove("disable-scroll");
            }}
            style={{ position: "absolute", top: 0, right: 0 }}
          >
            <XIcon w={24} h={24} />
          </span>
        </div>
      ) : null}
      {modalType === "hint" || modalType === "task description" ? (
        <div
          style={{
            alignSelf: "start",
            flex: "1 0 auto",
            display: "flex",
            flexDirection: "column",
            gap: "42px",
          }}
        >
          {modalType === "hint"
            ? renderHintText(taskHintData.content)
            : renderHintText(taskDescription.content)}
        </div>
      ) : null}
      <ModalIcons
        type={modalType}
        themeIconStatus={themeIconStatus}
        w={80}
        h={80}
      />
      {modalType === "error" ||
      modalType === "correct" ||
      modalType === "incorrect" ? (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "8px",
            textAlign: "center",
            flex: "1 0 auto",
          }}
        >
          <p
            style={{
              lineHeight: "32px",
              fontSize: "24px",
              fontWeight: "700",
              margin: "0",
            }}
          >
            {modalType === "incorrect" ? errors : ""} {text}
          </p>
          {modalType !== "correct" ? (
            <p
              style={{
                lineHeight: "24px",
                fontSize: "16px",
                margin: "0",
              }}
            >
              {text2}
            </p>
          ) : null}
        </div>
      ) : null}
      {modalType !== "correct" ? (
        <button
          className="modal-close"
          onClick={() => {
            setActive(false);
            document.body.classList.remove("disable-scroll");
          }}
        >
          {modalType !== "task description" ? "К заданию" : "Назад"}
        </button>
      ) : (
        <button
          className="modal-close"
          onClick={() => {
            clickHandler();
            document.body.classList.remove("disable-scroll");
          }}
        >
          К заданиям
        </button>
      )}
    </div>
  );
}
