import RenderTaskElement from "../RenderTaskElement/RenderTaskElement";

export default function SubRender({
  content,
  AnswerHanlder2,
  isOpenPopUp,
  setIsOpenPopUp,
  result,
  setResult,
  userAnswers,
  setUserAnswers,
  isSelected,
  setIsSelected,
}) {
  let index = 0;
  return content.map((task, indexOfTask) => {
    if (task.list) {
      return (
        <ul
          style={{ display: "flex", flexDirection: "column", gap: "12px" }}
          className={task.class ? task.class : ""}
        >
          {task.text.split("\n").map((paragraph, indexOfParagraph) => {
            return (
              <li style={{ paddingLeft: "20px", position: "relative" }}>
                {paragraph
                  .split(" ")
                  .map((word, indexOfWorld, arrayOfWords) => {
                    if (arrayOfWords.length !== indexOfWorld + 1) {
                      return (
                        <RenderTaskElement
                          word={word}
                          id={indexOfWorld}
                          paragraph={task.paragraph + indexOfParagraph}
                          AnswerHanlder2={AnswerHanlder2}
                          userAnswers={userAnswers}
                          setUserAnswers={setUserAnswers}
                          isOpenPopUp={isOpenPopUp}
                          setIsOpenPopUp={setIsOpenPopUp}
                          result={result}
                          setResult={setResult}
                          isSelected={isSelected}
                          setIsSelected={setIsSelected}
                          key={Math.random()}
                        />
                      );
                    } else {
                      return <span>{word}</span>;
                    }
                  })}
                <span
                  style={{
                    position: "absolute",
                    color: "#4E82FA",
                    top: "0",
                    left: "0",
                    fontWeight: "700",
                  }}
                >
                  {task.ol ? (
                    <span
                      style={{
                        position: "absolute",
                        width: "8px",
                        height: "2px",
                        left: "0",
                        top: "12px",
                        backgroundColor: "#4d83fa",
                      }}
                    ></span>
                  ) : (
                    ++index
                  )}
                  {task.ol ? "" : "."}
                </span>
                {task.testContent ? (
                  <SubRender
                    content={task.testContent}
                    AnswerHanlder2={AnswerHanlder2}
                    isOpenPopUp={isOpenPopUp}
                    setIsOpenPopUp={setIsOpenPopUp}
                    result={result}
                    setResult={setResult}
                    userAnswers={userAnswers}
                    setUserAnswers={setUserAnswers}
                    isSelected={isSelected}
                    setIsSelected={setIsSelected}
                    key={Math.random()}
                  />
                ) : null}
              </li>
            );
          })}
        </ul>
      );
    }
    return (
      <task.wrap>
        {task.text.split("\n").map((paragraph, indexOfParagraph) => {
          return (
            <>
              {paragraph.split(" ").map((word, indexOfWorld, arrayOfWords) => {
                if (arrayOfWords.length !== indexOfWorld + 1) {
                  return (
                    <RenderTaskElement
                      word={word}
                      id={indexOfWorld}
                      paragraph={task.paragraph + indexOfParagraph}
                      AnswerHanlder2={AnswerHanlder2}
                      userAnswers={userAnswers}
                      setUserAnswers={setUserAnswers}
                      isOpenPopUp={isOpenPopUp}
                      setIsOpenPopUp={setIsOpenPopUp}
                      result={result}
                      setResult={setResult}
                      isSelected={isSelected}
                      setIsSelected={setIsSelected}
                      key={Math.random()}
                    />
                  );
                } else {
                  return <span>{word}</span>;
                }
              })}
            </>
          );
        })}
        {task.testContent ? (
          <SubRender
            content={task.testContent}
            AnswerHanlder2={AnswerHanlder2}
            isOpenPopUp={isOpenPopUp}
            setIsOpenPopUp={setIsOpenPopUp}
            result={result}
            setResult={setResult}
            userAnswers={userAnswers}
            setUserAnswers={setUserAnswers}
            isSelected={isSelected}
            setIsSelected={setIsSelected}
            key={Math.random()}
          />
        ) : null}
      </task.wrap>
    );
  });
}
