export default function RenderTaskElement({
  word,
  id,
  paragraph,
  AnswerHanlder2,
  isOpenPopUp,
  setIsOpenPopUp,
  userAnswers,
  setUserAnswers,
  result,
  setResult,
  isSelected,
  setIsSelected,
}) {
  return (
    <span
      className={
        result[paragraph][id] === true
          ? "true-answer"
          : result[paragraph][id] === null
          ? "not-selected-answer"
          : result[paragraph][id] === false &&
            typeof isSelected[paragraph][id] !== "boolean"
          ? "false-answer"
          : ""
      }
      data-id={id}
      data-paragraph={paragraph}
      onClick={(event) => {
        AnswerHanlder2(
          id,
          paragraph,
          setIsOpenPopUp,
          userAnswers,
          setUserAnswers,
          isOpenPopUp
        );
      }}
      style={{
        position: "relative",
        cursor: "pointer",
      }}
    >
      <span
        className={
          isOpenPopUp && isOpenPopUp[0] === id && isOpenPopUp[1] === paragraph
            ? "active-word"
            : isSelected[paragraph][id]
            ? "selected-word"
            : ""
        }
      >
        {word}{" "}
      </span>
      {isOpenPopUp && isOpenPopUp[0] === id && isOpenPopUp[1] === paragraph ? (
        <PopUpWindow
          setIsOpenPopUp={setIsOpenPopUp}
          setUserAnswers={setUserAnswers}
          id={id}
          paragraph={paragraph}
          isOpenPopUp={isOpenPopUp}
          isSelected={isSelected}
          setIsSelected={setIsSelected}
        />
      ) : null}
    </span>
  );
}
function PopUpWindow({
  setIsOpenPopUp,
  setUserAnswers,
  id,
  paragraph,
  isOpenPopUp,
  isSelected,
  setIsSelected,
}) {
  function writeAnswer(
    event,
    setUserAnswers,
    id,
    paragraph,
    setIsOpenPopUp,
    isOpenPopUp,
    isSelected,
    setIsSelected
  ) {
    const answer = Number(event.target.dataset.answer);
    if (answer === 1) {
      setIsSelected((prevValue) => {
        prevValue[paragraph][id] = true;
        return prevValue;
      });
    }
    if (answer === 0) {
      setIsSelected((prevValue) => {
        prevValue[paragraph][id] = false;
        return prevValue;
      });
    }
    setUserAnswers((arrayOfUserAnswers) => {
      arrayOfUserAnswers[paragraph][id] = answer;
      return arrayOfUserAnswers;
    });
  }

  return (
    <div
      style={{
        position: "absolute",
        top: "-84px",
        left: "50%",
        boxShadow:
          "0px 6px 16px 0px rgba(230, 235, 245, 0.80), 0px 1px 4px 0px #E3EBFC",
        backgroundColor: "#fff",
        zIndex: "1",
        display: "flex",
        flexDirection: "column",
        lineHeight: "24px",
        borderRadius: "12px",
        translate: "-50% 0",
        overflow: "hidden",
      }}
      onClick={(event) => {
        writeAnswer(
          event,
          setUserAnswers,
          id,
          paragraph,
          setIsOpenPopUp,
          isOpenPopUp,
          isSelected,
          setIsSelected
        );
      }}
    >
      <button
        data-answer="1"
        style={{
          color: "#0D4CD3",
          padding: "6px 10px",
          transitionDuration: "300ms",
          whiteSpace: "nowrap",
        }}
        className="answer-button"
      >
        Поставить
      </button>
      <button
        data-answer="0"
        style={{
          color: "#E11432",
          padding: "6px 10px",
          transitionDuration: "300ms",
          whiteSpace: "nowrap",
        }}
        className="answer-button"
      >
        Не ставить
      </button>
    </div>
  );
}
