import "./index.css";
import "./App.css";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer.jsx";
import Main from "./components/Main/Main";
import { useState } from "react";
export default function App() {
  const styles = {
    colors: {},
    background: {
      main: "#f9fcff",
    },
    font: {
      header: {
        fontWeight: "700",
        fontSize: "24px",
        fontHeight: "32px;",
      },
    },
  };
  const setDefaultIconTheme = () => {
    localStorage.setItem("themeIcon", 0);
    return 0;
  };
  const [themeIconStatus, changeThemeIconStatus] = useState(
    localStorage.getItem("themeIcon")
      ? Number(localStorage.getItem("themeIcon"))
      : setDefaultIconTheme()
  );
  const [active, setActive] = useState(false);
  return (
    <>
      <Header
        themeIconStatus={themeIconStatus}
        changeThemeIconStatus={changeThemeIconStatus}
      />
      <Main
        background={styles.background.main}
        themeIconStatus={themeIconStatus}
        active={active}
        setActive={setActive}
      />
      <Footer />
    </>
  );
}
