import { useState } from "react";
import TaskListElement from "../TaskListElement/TaskListElement";
import Task from "../Task/Task";
import { ArrowLeft } from "../Icons/Icons";
import { taskListHeader } from "../../data.js";
export default function TaskList({
  themeIconStatus,
  setIsAgree,
  active,
  setActive,
}) {
  const [currentTask, setCurrentTask] = useState(null);
  const [isOpenTask, setIsOpenTask] = useState(false);
  const [isOpenTaskPage, setIsOpenTaskPage] = useState(false);
  if (isOpenTaskPage) {
    return (
      <Task
        setIsOpenTaskPage={setIsOpenTaskPage}
        setCurrentTask={setCurrentTask}
        currentTask={currentTask}
        themeIconStatus={themeIconStatus}
        active={active}
        setActive={setActive}
      />
    );
  }
  if (!isOpenTask && !isOpenTaskPage) {
    return (
      <section style={{ marginTop: "-28px" }}>
        <button
          onClick={() => setIsAgree(false)}
          style={{ display: "flex", alignItems: "center", color: "#0d4cd3" }}
        >
          <ArrowLeft h={"18px"} /> Назад
        </button>
        <p
          style={{
            fontWeight: "700",
            fontSize: "24px",
            fontHeight: "32px",
          }}
        >
          Выберите тему задания
        </p>
        <div className="task-container">
          {taskListHeader.map((task) => {
            return (
              <TaskListElement
                taskName={task.name}
                key={task.id}
                taskId={task.id}
                setIsOpenTask={setIsOpenTask}
                setIsOpenTaskPage={setIsOpenTaskPage}
                setCurrentTask={setCurrentTask}
              />
            );
          })}
        </div>
      </section>
    );
  }
}
